footer {
  background: var( --color-bg-variant);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

.footer_logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 3rem;
  margin: 0 auto 3rem;
}

.footer_socials {
  display: flex;
  justify-content: center;
  gap: 3rem;
  margin-bottom: 4rem;
}

.footer_socials a {
  background: var(--color-primary-variant);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
  transition: var(--transition);
}

.footer_socials a:hover {
  background: transparent;
  border: 1px solid var(--color-bg-variant);
  color: var(--color-bg);
}

.footer_copyright {
  margin-bottom: 4rem;
}

/* ===========MEDIA QUERIES (SMALL DEVICES)=========== */

@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1rem;
  }
  .footer_socials {
    margin-bottom: 2rem;
  }
}
